import React, { Component } from 'react'
import Router from 'next/router'
import nextCookie from 'next-cookies'
import { actionTypes, URL } from './types'
import fetch from 'isomorphic-unfetch';

export const login = (username, password) => async dispatch => {
    const response = await fetch(`${URL}/auth/login/`, {
        method: 'POST',
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            'target': 'auth',
            'action': 'login',
            'depth': 1,
            'model': {
                username,
                password
            }
        })
    });
    if(response.ok){
        Router.push('/');
        let state = dispatch({
            type: actionTypes.LOGIN_SUCCESS,
            payload: await response.json()
        });
        state = state.payload;
        dispatch({
            type: actionTypes.GLOBAL_GET_PROCESS,
            payload: state.profile.process
        });
        dispatch({
            type: actionTypes.GLOBAL_SET_PROCESS,
            payload: state.profile.process[0].id
        });
        dispatch({
            type: actionTypes.GLOBAL_GET_CANDIDATES,
            payload: state.profile.process[0].candidates
        });
        dispatch({
            type: actionTypes.GLOBAL_SET_CANDIDATE,
            payload: state.profile.process[0].candidates[0].id
        });
        return state;
    } else {
        return dispatch({type: actionTypes.AUTH_ERROR});
    }
};

export const logout = (token) => async dispatch => {
    const response = await fetch('http://10.0.0.5:8000/auth/logout/', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`
        },
        body: null
    });
    Router.push('/login');
    dispatch({ type: actionTypes.LOGOUT_SUCCESS });
    dispatch({ type: actionTypes.DELETE_NOMINAL_LIST });
    dispatch({ type: actionTypes.DELETE_PROMOTED});
};

// Gets the display name of a JSX component for dev tools
const getDisplayName = Component => Component.displayName || Component.name || 'Component';

export const withAuthSync = WrappedComponent => class extends Component {
    static displayName = `withAuthSync(${getDisplayName(WrappedComponent)})`;

    static async getInitialProps (ctx) {
        const token = auth(ctx);

        const componentProps =
            WrappedComponent.getInitialProps &&
            (await WrappedComponent.getInitialProps(ctx));

        return { ...componentProps, token }
    }

    constructor (props) {
        super(props);

        this.syncLogout = this.syncLogout.bind(this)
    }

    componentDidMount () {
        window.addEventListener('storage', this.syncLogout)
    }

    componentWillUnmount () {
        window.removeEventListener('storage', this.syncLogout);
        window.localStorage.removeItem('logout')
    }

    syncLogout (event) {
        if (event.key === 'logout') {
            console.log('logged out from storage!');
            Router.push('/login')
        }
    }

    render () {
        return <WrappedComponent {...this.props} />
    }
};

export const auth = ctx => {
    const { token } = nextCookie(ctx);

    /*
     * This happens on server only, ctx.req is available means it's being
     * rendered on server. If we are on server and token is not available,
     * means user is not logged in.
     */
    if (ctx.req && !token) {
        ctx.res.writeHead(302, { Location: '/login' });
        ctx.res.end();
        return
    }

    // We already checked for server. This should only happen on client.
    if (!token) {
        Router.push('/login')
    }

    return token
};